.about {
  background: linear-gradient(135deg, #f3f4f6, #e5e7eb);
  padding: 5rem 2rem;
  color: #1f2937;
  text-align: center;
  font-family: 'Arial', sans-serif;
}

.about h2 {
  font-size: 2.5rem;
  color: #1e40af;
  margin-bottom: 2rem;
  position: relative;
  display: inline-block;
}

.about h2::after {
  content: '';
  display: block;
  width: 60px;
  height: 4px;
  background: #2563eb;
  margin: 0.5rem auto;
  border-radius: 2px;
}

.summary-content {
  max-width: 900px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
  text-align: left;
}

.main-summary {
  font-size: 1.2rem;
  line-height: 1.8;
  color: #374151;
  animation: fade-in 1.5s ease-in-out;
}

.key-highlights h3,
.certifications h3 {
  font-size: 1.5rem;
  color: #2563eb;
  margin-bottom: 1rem;
}

.key-highlights ul,
.certifications ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.key-highlights li,
.certifications li {
  margin-bottom: 1rem;
  line-height: 1.8;
  font-size: 1.1rem;
  color: #4b5563;
  padding-left: 1.5rem;
  position: relative;
}

.key-highlights li::before,
.certifications li::before {
  content: 'âœ”';
  color: #2563eb;
  position: absolute;
  left: 0;
  font-size: 1.2rem;
}

.key-highlights li strong {
  color: #1e40af;
  font-weight: 600;
}

.certifications li {
  font-style: italic;
  animation: slide-up 1.5s ease-in-out;
}

/* Animations */
@keyframes fade-in {
  from {
    opacity: 0;
    transform: translateY(10%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slide-up {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .summary-content {
    padding: 0 1rem;
  }

  .about h2 {
    font-size: 2rem;
  }

  .main-summary {
    font-size: 1rem;
  }

  .key-highlights li,
  .certifications li {
    font-size: 1rem;
  }
}
